@Composable
fun NavigationDrawerView(
    uiState: UiState,
    onTriggerEvent: (UiEvents) -> Unit,
    navController: NavHostController,
    scaffoldState: ScaffoldState,
    scope: CoroutineScope
) {
    val uriHandler = LocalUriHandler.current
    Column(
        modifier = Modifier.fillMaxHeight().width(400.dp).background(color = white),
        horizontalAlignment = Alignment.CenterHorizontally
    ) {
        Spacer(Modifier.height(15.dp))
        CommonImage(painterResource(splash), modifier = Modifier.width(200.dp).height(200.dp))
        Spacer(Modifier.height(15.dp))
        Text24SemiBold("GSB-VyaparBhagya", black)
        Spacer(Modifier.height(15.dp))
        Column(
            modifier = Modifier.verticalScroll(rememberScrollState()).fillMaxWidth().padding(16.dp),
            horizontalAlignment = Alignment.CenterHorizontally
        ) {
            val user = uiState.getUser()
            if (user.getId().isEmpty()) {
                GVBButton(text = "Login") {
                    launchLoginMobile()
                    scope.launch { scaffoldState.drawerState.close() }
                }
                Spacer(Modifier.height(15.dp))
            } else {
                Row(
                    modifier = Modifier.height(110.dp).fillMaxWidth().background(offwhite)
                        .padding(16.dp).clickable {
                        checkUserAfterLogin()
                        scope.launch { scaffoldState.drawerState.close() }
                    }, verticalAlignment = Alignment.CenterVertically
                ) {
                    uiState.getImage(user.getImage().ifEmpty { BLANK_PROFILE.value() })?.let { imageBitmap -> CommonImage(imageBitmap, 80.dp, 80.dp) }
                    Spacer(Modifier.width(15.dp))
                    Column(modifier = Modifier.fillMaxWidth(), verticalArrangement = Arrangement.Center) {
                        Text14RegularBB("${user.getFirstName()} ${user.getLastName()}", black)
                        Text14RegularBB(user.getMobile(), black)
                        Text14RegularBB(user.getEmail(), black)
                    }
                }
                Spacer(Modifier.height(15.dp))
                GVBButton(text = "Manage Business") {
                    scope.launch { scaffoldState.drawerState.close() }
                }
                Spacer(Modifier.height(15.dp))
                GVBButton(text = "Sign Out") {
                    logout()
                }
                Spacer(Modifier.height(15.dp))
                Row(modifier = Modifier.fillMaxWidth().clickable { uriHandler.openUri(uiState.getInfo().getWebsite()) }, horizontalArrangement = Arrangement.Start, verticalAlignment = Alignment.CenterVertically) {
                    Icon(imageVector = Icons.Filled.ThumbUp, contentDescription = "ThumbUp", tint = black)
                    Spacer(Modifier.width(8.dp))
                    Text14RegularB("Support Us", black)
                }
                Spacer(Modifier.height(15.dp))
                Row(modifier = Modifier.fillMaxWidth().clickable { uriHandler.openUri(uiState.getInfo().getWebsite()) }, horizontalArrangement = Arrangement.Start, verticalAlignment = Alignment.CenterVertically) {
                    Icon(imageVector = Icons.Filled.ThumbUp, contentDescription = "ThumbUp", tint = black)
                    Spacer(Modifier.width(8.dp))
                    Text14RegularB("Support Us", black)
                }
                Spacer(Modifier.height(15.dp))
            }
        }
    }
}